<template>
  <div>
    <Header></Header>
    <router-view></router-view>
  </div>
</template>
<script>
  import {getStore, setStore} from "../../utils/util"
  import {mapState, mapMutations} from "vuex"
  export default {
    data() {
      return {

      }
    },
    computed: {
      ...mapState(["hasLogin", "userInfo"])
    },
    mounted() {
      this.getUserData();
    },
    methods: {
      ...mapMutations(["INIT_USER", "CHANGE_LOGIN"]),

      //判断本地是否存储有用户名密码
      getUserData:function () {
        var localuserInfo = getStore("userInfo");
        if (!localuserInfo) {
          this.CHANGE_LOGIN(false);
          return;
        }else  {
          localuserInfo = JSON.parse(localuserInfo);
          this.username1 = localuserInfo.username;
          this.password1 = localuserInfo.password;
//          this.CHANGE_LOGIN(true);
          this.INIT_USER();
        }
      }
    }
  }
</script>



